

var wu = {
    initWebUploader: function (selector, typ, lon, lat, callback) {

        var percentages = {};

        var uploader = WebUploader.create({
            auto: true,
            server: 'http://47.109.38.99:9081/api/ticket',
            pick: selector + ' .upload-pick',
            //chunked: true,
            //chunkSize: 512 * 1024,
            fileNumLimit: 1,
            resize: false,
            accept: {
                title: 'Images',
                extensions: 'gif,jpg,jpeg,bmp,png',
                mimeTypes: 'image/*'
            },
            formData: {
                typ: typ,
                lon: lon,
                lat: lat
            }
        });

        uploader.on('uploadBeforeSend', function (obj, data, headers) {
            $.extend(headers, {
                "Access-Control-Allow-Origin": "*"
            });
        });

        // var updatepick = function () {
        //     if (maxcount === undefined || maxcount === null) {
        //         return ;
        //     }
        //     // console.log('updatepick - ', $(selector + ' li').length, maxcount);
        //     if ($(selector + ' li').length > maxcount) {
        //         $(selector + ' .upload-pick').addClass('hidden');
        //     } else {
        //         $(selector + ' .upload-pick').removeClass('hidden');
        //     }
        // };

        uploader.onFileQueued = function (file) {

            $(selector + ' .upload-pick').addClass('hidden');

            var $li = $(selector);
            // var $wrap = $li.find('div.imageWrapper');
            // var $stat = $li.find('div.state-overlay');
            // var $delete = $li.find('div.delete');

            var $progress = $li.find('div.upload-progress');
            var $progstep = $li.find('div.upload-progress-step');
            var $progtext = $li.find('div.upload-progress-text');

            var showProgress = function () {
                $progress.removeClass('hidden');
                // $inforact.addClass('hidden');
                // $stat.removeClass('hidden');
            };
            // var showInforact = function () {
            //     $progress.addClass('hidden');
            //     $inforact.removeClass('hidden');
            //     $stat.removeClass('hidden');
            // };

            var showError = function (code) {
                // showInforact();
                if (code === 'interrupt') {
                    // $inforact.html('<i class="fa fa-pause-circle"></i>');
                    $progtext.html('Uploading has been interrupted.')

                    $(selector + ' .upload-pick').removeClass('hidden');
                } else if (code === 'reset') {
                    // circle-o-notch  sun-o
                    // $inforact.html('<i class="fa fa-cog fa-spin"></i>');
                    $progtext.html('Uploading in process, please wait...')
                    $progstep.animate({ 'width': '0%' }, 500);
                } else {
                    console.log('File upload failed: ' + code);
                    // #ff3b30  style="color: rgba(255, 59, 48, 0.75);"
                    // $inforact.html('<i class="fa fa-exclamation-circle"></i>');
                    $progtext.html('Uploading failed.')

                    $(selector + ' .upload-pick').removeClass('hidden');
                }
            };

            if (file.getStatus() === 'invalid') {
                showError(file.statusText);
            } else {
                /*
                $wrap.html(
                    '<div style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);">' +
                        '<i class="fa fa-spinner fa-spin" style="color: #8e8e93;"></i>' +
                    '</div>');
                /*/
                showError('reset');
                //*/
                // uploader.makeThumb(file, function (error, src) {
                //     if (error) {
                //         /*
                //         $wrap.html(
                //             '<div style="position: absolute; left: 50%; top: 50%; transform: translate(-50%, -50%);">' +
                //                 '<i class="fa fa-exclamation-triangle" style="color: #ff3b30;"></i>' +
                //             '</div>');
                //         /*/
                //         console.log('makeThumb: %o', error)
                //         showError('thumbnail');
                //         //*/
                //         return ;
                //     }
                //     var img = $('<img src="' + src + '">');
                //     $wrap.empty().append(img);
                // }, thumbnailw, thumbnailh);

                percentages[file.id] = 0;
            }

            file.on('statuschange', function (cur, prev) {
                console.log('statuschange ' + prev + ' > ' + cur);
                if (cur === 'error' || cur === 'invalid') {
                    showError(file.statusText);
                    percentages[file.id] = 1;
                } else if (cur === 'interrupt') {
                    showError('interrupt');
                } else if (cur === 'queued') {
                    showError('reset');
                    percentages[file.id] = 0;
                } else if (cur === 'progressing') {
                    showProgress();
                } else if (cur === 'complete') {
                    $progtext.html('Uploaded.');
                    $progress.promise().done(function () {
                        $progstep.animate({ 'width': '100%' }, 500);
                    });
                }

                // $li.removeClass('state-' + prev).addClass('state-' + cur);
            });

            // $stat.on('click', function () {
            //     if ($li.hasClass('state-error')) {
            //         showError('reset');
            //         setTimeout(function () {
            //             uploader.retry(file);
            //         }, 1000);
            //     }
            // });

            // $delete.on('click', function () {
            //     uploader.removeFile(file);
            // });

            // $li.insertBefore(selector + ' .webuploader-container');

            // updatepick();
        };

        uploader.onFileDequeued = function (file) {
            // var $li = $('#' + file.id);
            // $li.off().children().off().end().stop().remove();

            delete percentages[ file.id ];

            // updatepick();
        };

        uploader.onUploadProgress = function (file, percentage) {
            console.log('file: ' + file.id + ' (' + percentage + ')');

            var $li = $(selector);
            var $progress = $li.find('div.upload-progress-step');

            file.setStatus('progressing');

            // var $percents = {};
            // $progress.children().each(function (index, element) {
            //     $percents[index] = $(element);
            // });

            var clamp = function (min, val, max) {
                return Math.min(Math.max(min, val), max);
            }

            percentage = clamp(0, percentage, 1);
            var oldpercentage = percentages[file.id];
            percentages[file.id] = percentage;

            if (percentage != oldpercentage) {
                $progress.animate({
                    'width': (100 * percentage) + '%',
                }, {
                    duration: 1800 * (percentage - oldpercentage)
                });
            }
        };

        uploader.onUploadSuccess = function (file, response) {
            console.log(response);
            callback((response != undefined && response != null), response)
        };

        uploader.onStartUpload = function () {
            // disableSubmit();
        };

        uploader.onUploadFinished = function () {
            // enableSubmit();
        };
    },
    getUploadList: function (selector) {
        var uploadlist = []
        $(selector).find('li').each(function () {
            var uuid = $(this).data('uuid');
            if (uuid !== undefined && uuid !== null) {
                uploadlist.push(uuid);
            }
        });
        //console.log(uploadlist);
        return uploadlist;
    },

};

export default wu;